import isEqual from 'lodash/isEqual'

export default (openNodes, url) => {
  if (!url) return false
  if (!openNodes) return false
  if (!openNodes.some) return false

  console.log('isNodeOpen, 1 of 2')
  const nodeIsOpen = openNodes.some(n => isEqual(n, url))
  console.log('isNodeOpen, 2 of 2')
  return nodeIsOpen
}
