import isEqual from 'lodash/isEqual'

export default (node, activeNodeArray) => {
  if (!node) return false
  if (!node.url) return false
  if (!activeNodeArray) return false
  const activeNodeArrayPartWithEqualLength = activeNodeArray.slice(
    0,
    node.url.length,
  )
  console.log('isNodeInActiveNodePath, 1 of 2')
  const nodeIsInActiveNodePath = isEqual(
    activeNodeArrayPartWithEqualLength,
    node.url,
  )
  console.log('isNodeInActiveNodePath, 2 of 2')

  return nodeIsInActiveNodePath
}
